import React from 'react';
import { ThemeProvider } from 'styled-components';
import MainContainer from './containers/MainContainer';

const DarkTheme = {
  name: 'DarkTheme',
  backgroundColor: '#282c34',
  fontColor: 'white',
  borderColor: 'rgba(255,255,255,0.5)',
  primaryColor: '#f95550',
  primaryColorLight: '#ec6763',
  primaryColorDark: '#dc4641',
  secondaryColor: '#34c5dc',
  secondaryColorLight: '#60ecec',
  secondaryColorDark: '#1992ad',
  startSay: '!',
  says: [
    '',
    '',
    '',
    '',
    ' 500g,  3,  50cc,  20ml,  ,  2.3,  2,  10g',
    '',
    '',
    '',
    '',
    ' 6,  50g,  1, / '
  ]
};
const LightTheme = {
  name: 'LightTheme',
  backgroundColor: '#f3f1ee',
  fontColor: '#1d1f23',
  borderColor: 'rgba(200,129,0,0.1)',
  primaryColor: '#FFA20F',
  primaryColorLight: '#fbb941',
  primaryColorDark: '#fb910d',
  secondaryColor: '#34c5dc',
  secondaryColorLight: '#60ecec',
  secondaryColorDark: '#1992ad',
  startSay: '~',
  says: [
    '!',
    '',
    '?',
    '',
    '?',
    '',
    '',
    '',
    '?',
    ' 2 ,  1 , / 1 , / '
  ]
};
const GrassBag = {
  name: 'GrassBag',
  backgroundColor: '#33261c',
  fontColor: '#ffe76c',
  borderColor: '#0e132b',
  primaryColor: '#eae5db',
  primaryColorLight: '#fbb941',
  primaryColorDark: '#dad2c3',
  secondaryColor: '#34c5dc',
  secondaryColorLight: '#60ecec',
  secondaryColorDark: '#1992ad',
  startSay: '',
  says: [
    '!',
    '0!100!',
    '!!!',
    '!!',
    '?',
    '~~',
    ' ',
    '',
    'ZZZ',
    '',
    '',
    '',
    '',
    '',
    ''
  ]
};

class App extends React.PureComponent {
  constructor() {
    super();
    this.state = {
      theme: DarkTheme
    };
  }
  onChangeTheme = item => {
    switch (item) {
      case 'DarkTheme':
        return this.setState({ theme: DarkTheme });
      case 'LightTheme':
        return this.setState({ theme: LightTheme });
      case 'GrassBag':
        return this.setState({ theme: GrassBag });
      default:
        return this.setState({ theme: DarkTheme });
    }
  };
  render() {
    return (
      <ThemeProvider theme={this.state.theme}>
        <MainContainer onChangeTheme={this.onChangeTheme} theme={this.state.theme}/>
      </ThemeProvider>
    );
  }
}

export default App;
